.button_body{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    vertical-align: middle;
    display: inline-block;
    pointer-events: none;
}

.button_container{
    position: relative;
    width: 100%;
    height: 100%;
    display: inline-block;
    pointer-events: none;
}

.button_container_inner{
    position: absolute;
    width: 100%;
    top: 50%; left: 50%;
    transform: translate(-50%,-50%);
    pointer-events: none;
}

.button{
    position: relative;
    border: 5px solid lightgrey;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    width: 40px;
    height: 40px;
    pointer-events:all;
    outline: none;
    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
    -moz-user-select: none; 
    -webkit-user-select: none; 
    -ms-user-select:none; 
    user-select:none;
    -o-user-select:none;
}

.left{
    background-color: darkgray;
    float: left;
}

.right{
    background-color: darkgray;
    float: right;
}

.button_image{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    object-fit: contain;
}