body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#image_view_container{
    position: relative;
}

#image_view{
    width: 100%;
    height: 280px;
    object-fit: cover;
}

#image_click_navigation_container{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

#left{
    /*background-color: rgba(0, 0, 255, 0.247);*/
    position: absolute;
    height: 100%;
    width: 50%;
    left: 0;
}

#right{
    /*background-color: rgba(255, 0, 0, 0.274);*/
    position: absolute;
    height: 100%;
    width: 50%;
    right: 0;
}
.button_body{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    vertical-align: middle;
    display: inline-block;
    pointer-events: none;
}

.button_container{
    position: relative;
    width: 100%;
    height: 100%;
    display: inline-block;
    pointer-events: none;
}

.button_container_inner{
    position: absolute;
    width: 100%;
    top: 50%; left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    pointer-events: none;
}

.button{
    position: relative;
    border: 5px solid lightgrey;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    width: 40px;
    height: 40px;
    pointer-events:all;
    outline: none;
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
    -moz-user-select: none; 
    -webkit-user-select: none; 
    -ms-user-select:none; 
    user-select:none;
    -o-user-select:none;
}

.left{
    background-color: darkgray;
    float: left;
}

.right{
    background-color: darkgray;
    float: right;
}

.button_image{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    object-fit: contain;
}
.indicator {
    text-align: center;
    margin: 5px;
    outline: none;
    border-radius: 25px;
    color: darkgray;
    background-color: transparent;
    border: none;
}

.indicator:active {
    color: black;
    background-color: #d1c1ce;
    border: 1px solid darkgray;
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
}

.selected{
    color: black;
    background-color: white;
    border: 1px solid darkgray;
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
}
.indicator_container{
    width: 100%;
    /*display: inline-block;*/
    background-color: rgba(211, 211, 211, 0.205);
    text-align:center;
}
.body{
    
}

.image_section{
    position: relative;
}

.description{
    position: relative;
    width: 100%;
    text-align: center;
    border: 1px solid lightgray;
}
